import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CustomData } from 'core/autocomplete/custom_data';
import { Constants } from 'core/constants';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { UserProfile } from 'core/models/userprofile';
import { LocalStorageService } from 'core/service/local-storage.service';
import { UserSession } from 'core/sessions';

@Component({
  selector: 'app-add-manual-transaction',
  templateUrl: './add-manual-transaction.component.html',
  styleUrls: ['./add-manual-transaction.component.css']
})
export class AddManualTransactionComponent implements OnInit,HttpSuccesFailureResponse {
  addManualTransaction: FormGroup;
  public customData: CustomData;
  gymId:any;
  isMasterGym:any;
  userData:UserProfile;
  
  public userSearch:any;
  constructor(private user_http: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService,public formBuilder: FormBuilder,private router: Router) {
    this.customData = new CustomData(this.http_userauth);

   }
  ngOnInit() {
    this.gymId = LocalStorageService.getUserId();
    this.isMasterGym = LocalStorageService.isMasterGym();
     this.initForm();
     this.isExistingCustomerTransaction('EXISTING');
  }
  initForm(userType?){
    this.userSearch='';
    this.addManualTransaction = this.formBuilder.group({
      email: ['', []],
      name: ['', []],
      mobile: ['', []],
      transactionTitle:['',[]],
      amount:[''],
      referenceId:[''],
      gymId:[this.gymId],
      userId:[''],
      userType:[userType?userType:'EXISTING'],
      errorMessage:['']
    });
  }
  isExistingCustomerTransaction(value){
    this.initForm(value);
    if(value=='EXISTING'){

      this.applyValidatorsAndState({
        email: {validators:[Validators.required], disabled: false },

        name: {validators:[Validators.required], disabled: false },
        amount: {validators:[Validators.required], disabled: false },
        transactionTitle:{validators:[Validators.required], disabled: false }
      });

    }else{
      this.applyValidatorsAndState({
        name: {validators:[Validators.required], disabled: false },
        amount: {validators:[Validators.required], disabled: false },
        transactionTitle:{validators:[Validators.required], disabled: false },
        email: {validators:[], disabled: false }

      });
      //this.addManualTransaction.get('name').enable();

    }

  }
  onUserSelected(event){
    try{
      this.http_userauth.getUserProfileByEmail(event.originalObject.email,this,true)

    }catch(e){

    }

  }
  addTransaction(){
    if(this.addManualTransaction.valid){
     let request = UserSession.cleanObject(this.addManualTransaction.value) ;
     console.log(request);
     this.http_userauth.addManualTransaction(request,this,true);

    }else{
      UserSession.markFormGroupTouched(this.addManualTransaction);
    }
  }
  onSuccess(type: any, responsedata: any) {
    if(type == Constants.findUserByEmailApi){
      this.addManualTransaction.patchValue({errorMessage:''});
      this.addManualTransaction.updateValueAndValidity();
       this.userData = responsedata;
       this.addManualTransaction.patchValue({name:this.userData.data.firstName});
       this.addManualTransaction.patchValue({email:this.userData.data.email});

       this.addManualTransaction.patchValue({mobile:this.userData.data.mobile});
       this.addManualTransaction.patchValue({userId:this.userData.data.id});
       this.addManualTransaction.updateValueAndValidity();



    }  
    if(type == Constants.manualTransactionApi){
      alert("Transaction added succefully.");

      this.router.navigate(['/gym/transaction']); 

    }
  }
  onFailure(type: any, response: any) {
     
    if(type == Constants.manualTransactionApi){
      alert(response);
    }
  
  }
  applyValidatorsAndState(fields: { [key: string]: { validators: any[], disabled?: boolean } }): void {
    Object.keys(fields).forEach(field => {
      const fieldConfig = fields[field];
      const control = this.addManualTransaction.get(field);
      if (control) {
        // Set validators
        control.setValidators(fieldConfig.validators || []);

        // Set disabled state if specified
        if (fieldConfig.disabled === true) {
          control.disable();
        } else if (fieldConfig.disabled === false) {
          control.enable();
        }
        control.updateValueAndValidity();

      }
    });
  }
}



