import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AddManualTransactionRoutingModule } from './add-manual-transaction-routing.module';
import { AddManualTransactionComponent } from './add-manual-transaction.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';
import { CommonComponentsModule } from 'common/common.module';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    CoreModule,
    CommonComponentsModule,
    AddManualTransactionRoutingModule
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ],

  declarations: [AddManualTransactionComponent]
})
export class AddManualTransactionModule { }



