import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddManualTransactionComponent } from './add-manual-transaction.component';

const routes: Routes = [
  { path: '', component: AddManualTransactionComponent }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddManualTransactionRoutingModule { }



